<template>
  <div>
      <van-nav-bar
        title="会员登录"
        left-arrow
        @click-left="$router.go(-1)"
      />
      <div class="title">
        <h2>手机号登录</h2>
      </div>
      <div class="SmallTitle">
        <h5>未注册的手机号登录后将自动注册</h5>
      </div>
      <div class="inpBox">
        <input class="mobile" placeholder="请输入手机号码" v-model="mobile">
        <hr class="hr1">
        <div class="CodeBox">
        <input class="Code" placeholder="请输入图形验证码" v-model="picCode">
        <img v-if="picUrl" :src="picUrl" class="CodeImg" @click="getCode"/>
        </div>
        <hr class="hr1">
        <div class="mobileCode">
        <input placeholder="请输入短信验证码" v-model="msgCode">
        <button class="btn-mc" @click="getTotalCode">{{ second === totalSecond ? '获取验证码' : '等待'+totalSecond+'秒后获取'  }}</button>
        </div>
        <hr class="hr1">
      </div>
      <div>
        <button class="loginBtn" @click="Login">登录</button>
      </div>
  </div>
</template>

<script>
import { getPicCode, getMsgCode, getLogin } from '@/api/login'
export default {
  name: 'LoginIndex',
  data () {
    return {
      picCode: '',
      picUrl: '',
      picKey: '',
      second: 60,
      totalSecond: 60,
      timmer: null,
      mobile: '',
      msgCode: ''
    }
  },
  async created () {
    this.getCode()
  },
  methods: {
    async getCode () {
      const res = await getPicCode()
      this.picUrl = res.data.base64
      this.picKey = res.data.key

      // this.$toast('获取验证码成功')
    },

    verifyFn () {
      if (!/0?(13|14|15|18)[0-9]{9}/.test(this.mobile)) {
        this.$toast('请输入正确的手机号')
        return false
      }
      if (!/^[A-Za-z0-9]{4}$/.test(this.picCode)) {
        this.$toast('请输入正确的验证码')
        return false
      }
      return true
    },
    async getTotalCode () {
      if (!this.verifyFn()) {
        return
      }
      if (!this.timmer && this.totalSecond === this.second) {
        await getMsgCode(this.picCode, this.picKey, this.mobile)
        this.$toast('短信发送成功,验证码为246810')
        this.timmer = setInterval(() => {
          this.totalSecond--
          if (this.totalSecond <= 0) {
            clearInterval(this.timmer)
            this.timmer = null
            this.totalSecond = this.second
          }
        }, 1000)
      }
    },
    async Login () {
      if (!this.verifyFn()) {
        return
      }
      if (!/^\d{6}$/.test(this.msgCode)) {
        this.$toast('请输入正确的手机验证码')
        return
      }
      const res = await getLogin(this.mobile, this.msgCode)
      console.log(res)
      this.$store.commit('user/getUserInfo', res.data)
      this.$toast('登录成功')
      const url = this.$route.query.backUrl || '/'
      this.$router.replace(url)
    }
  },
  destroyed () {
    clearInterval(this.timmer)
    // this.timmer = null
  }
}
</script>

  <style scoped lang="less">

  .title{
    height: 40px;
    margin-top:15px;
    margin-left: 25px;
    display: flex;
    flex-direction: column;
  }
  .SmallTitle{
    margin-left: 25px;
    color: grey;
  }
  .inpBox{
    display: flex;
    flex-direction: column;
    input{
      border: none;
      height: 50px;
      padding-left: 25px;
    }
    .CodeBox{
      display: flex;
      .Code{
        width: 190px;
      }
      .CodeImg{
        height: 40px;
        width: 100px;
        margin-left: 40px;
      }
    }
      .mobileCode{
        display: flex;
        input{
          width: 190px;
        }
      }
      .btn-mc{
        background-color: white;
        color: green;
        // height: 40px;
        border: none;
        margin-left: 60px;
      }
  }
  .hr1{
    width: 350px;
    height: 1px;
    background-color: grey;
    transform: scaleY(0.1);
  }
  .loginBtn{
    margin-left: 38px;
    margin-top: 30px;
    width: 299px;
    height: 60px;
    border-radius: 40px;
    border: none;
    background-color: rgb(253, 154, 22);
  }
  </style>
